import React from "react";
import { useState, useEffect } from "react";
import { useSelector } from "react-redux";

const ProductList = ({ products, onItemClick }) => {
  const [mobileMenu, setMobileMenu] = useState(false);
  const { category } = useSelector((state) => state.category)

  function onResize() {
    if (typeof window !== undefined) {
      if (window.innerWidth < 1200) {
        setMobileMenu(true);
      } else {
        setMobileMenu(false);
      }
    }
  }

  useEffect(() => {
    onResize();
    {
      window.addEventListener("resize", function () {
        onResize();
      });
    }
    // ApiCalled();
  }, []);

  useEffect(() => {
    if (document) {
      const scrollToDiv = document.getElementById(category)
      const divTop = scrollToDiv?.offsetTop - 100
      window.scrollTo(0, divTop)
    }
  }, [category, document])
  return (
    <div>
      <div className={mobileMenu === true ? "container" : ""}>
        <div className="product_category_section">
          {Object.keys(products).map((category, index) => (
            <div key={index} className="pt-3">
              <div className="productname_section" id={category.replace(" ", "_")}>
                <div className="container">
                  <p className="section-title py-2 reverse-title">{category}</p>
                </div>
              </div>
              <div className="container section-spacer-mini">
                <div className="row justify-content-center">
                  {products[category].map((product) => (
                    <div className="col-lg-4 col-md-6 my-2 p-0">
                      <div className="category_section_card mx-2 h-100">
                        <div
                          className="card h-100"
                          key={product.name}
                          onClick={() => onItemClick(product)}
                        >
                          <div className="card-body d-flex align-items-center justify-content-center">
                            <img src={product.Image} alt={product.Image} className="pro-image" style={{ objectFit: "contain" }} />
                          </div>
                          <div className="card-footer">
                            <p className="small-tiny-description text-darkess-blue">
                              {product.name}
                            </p>
                            <p className="small-tiny-description">
                            </p>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
      {/* {Object.keys(products).map((category) => (
        <div key={category}>
          <h2>{category}</h2>
          <div className="card-container">
            {products[category].map((product) => (
              <div
                className="card"
                key={product.name}
                onClick={() => onItemClick(product)}
              >
                <h3>{product.name}</h3>
                <p>Price: ${product.price}</p>
              </div>
            ))}
          </div>
        </div>
      ))} */}
    </div>
  );
};

export default ProductList;
